body {
background-color: rgb(34, 253, 224);

}
.container {
	width: 1600px;
	margin-left: auto;
	margin-right: auto;
	padding-right: 15px;
	padding-left: 15px;
 
 }
 .box{
	background-color: rgba(70, 240, 98, 0.3);
 }
 
 .title{
	text-align: center;
 }
 
 .search-form{
	text-align: center;
	margin-bottom: 50px;
 }
 
 .js-load-btn{
	display: flex;
	margin-left: auto;
	margin-right: auto;
	margin-top: 50px;
	margin-bottom: 50px;
 }
 
 .is-hidden {
	display: none;
 }
 
 .btn {
	padding: 8px;
	border-radius: 4px;
	background-color: rgb(61, 89, 248);
 }
 
 .btn:hover {
	background-color: rgb(32, 247, 12);
 }
 
 .input-form {
	padding: 8px;
	border-radius: 4px;
 }
 
 .gallery {
	display: flex;
	flex-wrap: wrap;
	list-style: none;
	margin: 0;
	padding: 0;
	margin-left: -20px;
	margin-top: -20px;
 }
 
 img {
	display: block;
	width: 100%;
	height: 250px;
 }
 
 .item {
	flex-basis: calc(100% / 4 - 20px);
	margin-left: 20px;
	margin-top: 20px;
 }
 
 .js-starts {
	display: flex;
	justify-content: space-around;
	border-color: rgb(74, 71, 255);
 }
 
 .photo-card:hover{
	box-shadow: 0px 2px 1px -1px rgba(255, 10, 10, 0.2),
	  0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
 }
 
 .back_to_top {
	position: fixed;
	bottom: 80px;
	right: 40px;
	z-index: 9999;
	width: 60px;
	height: 60px;
	text-align: center;
	line-height: 30px;
	background-color: rgb(248, 87, 122);
	color: rgb(28, 42, 230);
	cursor: pointer;
	border-radius: 35%;
	display: none;
 }
 
 .back_to_top:hover {
	background: #f7f336;
 }
 
 .back_to_top-show {
	display: block;
 }


